import React from 'react'
import { BrowserRouter as Router, Routes, Route, NavLink } from "react-router-dom";

function Createchild() {
  return (
    <div>
        <nav id="home" className="navbar navbar-expand-lg bg-body-tertiary">
        <div className="container-fluid">
            <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarTogglerDemo03" aria-controls="navbarTogglerDemo03" aria-expanded="false" aria-label="Toggle navigation">
            <span className="navbar-toggler-icon"></span>
            </button>
            <a className="navbar-brand" >
                <img src="images/wordmark.png" width="120px" alt="VaccAmate" className="d-inline-block align-text-top" />
              </a>
              
            <div className="collapse navbar-collapse " id="navbarTogglerDemo03">
                <ul className="navbar-nav me-auto mb-2 mb-lg-0"> </ul>

                
                <NavLink  to='/profilehp' className="nav-link"><b >Go back</b></NavLink>
                <NavLink  to='/adminlogin' className="btn btnn btn-primary" >Log Out</NavLink>

            </div>
        </div>
      </nav>

    <div className='middle'>
        <div className='left' style={{background: '#FFF7F3'}}>
            <img src="images/baby.png" width="229px" alt="" />
            <div>
                <h2 className='head' style={{color: '#AF6B52'}}>
                Empower your children's <br /> health: vaccinate them <br /> and keep them safe!
                </h2>
                <p className='para' style={{color: '#AF6B52'}}>
                It's important for children to be <br /> vaccinated so that they have the <br /> opportunity to become adults.                </p>
            </div>           
        </div>

        <div className='right'>
            
            <form className="row g-3 container">

            <h3>Create New Child Profile</h3>
            
            <div class="col-12">
                <label htmlfor="Name" className="form-label">Name</label>
                <input type="text" class="form-control" id="pName" placeholder="Name" required/>
            </div>

            <div className="col-md-6">
                <label htmlfor="Ahaar Number" className="form-label">Birth Certificate ID</label>
                <input type="Number" className="form-control" id="Aadhar" required/>
            </div>

            <div className="col-md-6">
                <label htmlfor="Phone Number" className="form-label">Date of Birth</label>
                <input type="date" className="form-control" id="PhoneNumber" required/>
            </div>

            <div className="col-12">
                <label htmlfor="inputAddress2" className="form-label">Gender</label>
                <div class="form-check">
                    <input className="form-check-input" type="radio" name="flexRadioDefault" id="flexRadioDefault1"/>
                    <label className="form-check-label" htmlfor="flexRadioDefault1">Male</label>
                </div>
                <div class="form-check">
                <input className="form-check-input" type="radio" name="flexRadioDefault" id="flexRadioDefault1"/>
                    <label className="form-check-label" htmlfor="flexRadioDefault1">Female</label>
                </div>
            </div>

            {/* <div className="col-md-6">
                <label htmlfor="inputPassword4" className="form-label">Create Password</label>
                <input type="password" className="form-control" id="inputPassword4" required/>
            </div>

            <div className="col-md-6">
                <label htmlfor="inputPassword4" className="form-label">Confirm Password</label>
                <input type="password" className="form-control" id="inputPassword4" required/>
            </div> */}

            <div className="col-12 d-grid gap-2 d-md-block">
                <NavLink   className="btn btn-outline-danger px-3 " >Cancel</NavLink>
                <NavLink   className="btn btn-primary mx-2 px-4" >Create</NavLink>
            </div>

            </form>

        </div>
    </div>
    </div>
  )
}

export default Createchild