import React from 'react'
import { BrowserRouter as Router, Routes, Route, NavLink } from "react-router-dom";
import jsPDF from 'jspdf';

function Child() {

  //   PDF CODE JSPDF PDF CODE JSPDF PDF CODE JSPDF

  const generatePDF = () => {
    const doc = new jsPDF();
     // Set the heading
    doc.setFontSize(16);
    const headingText = 'Immunization Record';
    const headingWidth = doc.getStringUnitWidth(headingText) * doc.internal.getFontSize() / doc.internal.scaleFactor;
    const headingX = (doc.internal.pageSize.getWidth() - headingWidth) / 2;
    doc.text(headingText, headingX, 10);

    // Set the details
    const name = 'Random Name';
    const age = 'Random Age';
    const parentName = 'Random Parent Name';
    const address = 'Random Address';

    doc.setFontSize(10);
    doc.text(`Name: ${name}`, 10, 30);
    doc.text(`Age: ${age}`, 10, 40);
    doc.text(`Parent Name: ${parentName}`, 10, 50);
    doc.text(`Address: ${address}`, 10, 60);

    // Set up the table
   
    const tableData = [
        ['Age', 'Vaccine Name', 'Due On', 'Given On', 'Given From'],

        ['2 years', 'Vaccine A', '2023-01-01', '2023-01-05', 'Hospital A'],
        ['       ', 'Vaccine B', '2023-02-01', '2023-02-05', 'Hospital B'],
        ['       ', 'Vaccine A', '2023-01-01', '2023-01-05', 'Hospital A'],
        ['       ', 'Vaccine B', '2023-02-01', '2023-02-05', 'Hospital B'],
        ['2 years', 'Vaccine A', '2023-01-01', '2023-01-05', 'Hospital A'],
        ['4 years', 'Vaccine B', '2023-02-01', '2023-02-05', 'Hospital B'],
        ['2 years', 'Vaccine A', '2023-01-01', '2023-01-05', 'Hospital A'],
        ['4 years', 'Vaccine B', '2023-02-01', '2023-02-05', 'Hospital B'],
        ['2 years', 'Vaccine A', '2023-01-01', '2023-01-05', 'Hospital A'],
        ['4 years', 'Vaccine B', '2023-02-01', '2023-02-05', 'Hospital B'],
        ['2 years', 'Vaccine A', '2023-01-01', '2023-01-05', 'Hospital A'],
        ['4 years', 'Vaccine B', '2023-02-01', '2023-02-05', 'Hospital B'],
        ['2 years', 'Vaccine A', '2023-01-01', '2023-01-05', 'Hospital A'],
        ['4 years', 'Vaccine B', '2023-02-01', '2023-02-05', 'Hospital B'],
        ['2 years', 'Vaccine A', '2023-01-01', '2023-01-05', 'Hospital A'],
        ['4 years', 'Vaccine B', '2023-02-01', '2023-02-05', 'Hospital B'],
        ['2 years', 'Vaccine A', '2023-01-01', '2023-01-05', 'Hospital A'],
        ['4 years', 'Vaccine B', '2023-02-01', '2023-02-05', 'Hospital B'],
        ['2 years', 'Vaccine A', '2023-01-01', '2023-01-05', 'Hospital A'],
        ['4 years', 'Vaccine B', '2023-02-01', '2023-02-05', 'Hospital B'],
        ['2 years', 'Vaccine A', '2023-01-01', '2023-01-05', 'Hospital A'],
        ['4 years', 'Vaccine B', '2023-02-01', '2023-02-05', 'Hospital B'],
        ['2 years', 'Vaccine A', '2023-01-01', '2023-01-05', 'Hospital A'],
        ['4 years', 'Vaccine B', '2023-02-01', '2023-02-05', 'Hospital B']
        
        // Add more rows as needed
    ];

    const tableTop = 80;
    const tableLeft = 10;
    const cellWidth = 35;
    const cellHeight = 8;

    // Draw the table headers
    doc.setFontSize(12);
    doc.setFont('bold');
    for (let i = 0; i < tableData[0].length; i++) {
        doc.text(tableData[0][i], tableLeft + i * cellWidth, tableTop);
    }

    // Draw the table rows
    doc.setFontSize(10);
    doc.setFont('normal');
    for (let i = 1; i < tableData.length; i++) {
        const rowData = tableData[i];
        for (let j = 0; j < rowData.length; j++) {
        doc.text(rowData[j], tableLeft + j * cellWidth, tableTop + i * cellHeight);
        }
    }

    

    const imgData = "images/wordmark.png"; // Use the imported images variable
    const imgWidth = 40; // Adjust the width of the image as needed
    const imgHeight = 7.5; // Adjust the height of the image as needed
    const imgX = (doc.internal.pageSize.getWidth() - imgWidth) / 2;
    const imgY = 13;
    doc.addImage(imgData, 'PNG', imgX, imgY, imgWidth, imgHeight);

    doc.save('immunization_record.pdf');
  };


  return (
    <div>

        <div className="container pt-5">
            <div className="card text-dark bg-light">
                <div className="card-body">
                    <h5 className="card-title">Zain Lubin</h5>
                    <p className="card-text">Next Vaccine on : 12/45/6009</p>
                    
                    <NavLink  to='/vaccrec' className="btn btn-primary" >View Vaccine Record</NavLink>
                   
                    <button onClick={generatePDF} className="btn btn-primary mx-2">Download Vaccine Record</button>
                </div>
            </div>
        </div>
        
        <br />

    </div>
  )
}

export default Child