import './Signin.css';
import { BrowserRouter as Router, Routes, Route, NavLink } from "react-router-dom";

import React from 'react'

function Signin() {
  return (
    <div>
        <nav id="home" className="navbar navbar-expand-lg bg-body-tertiary">
        <div className="container-fluid">
            <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarTogglerDemo03" aria-controls="navbarTogglerDemo03" aria-expanded="false" aria-label="Toggle navigation">
            <span className="navbar-toggler-icon"></span>
            </button>
            <a className="navbar-brand" >
                <img src="images/wordmark.png" width="120px" alt="VaccAmate" className="d-inline-block align-text-top" />
              </a>
              
            <div className="collapse navbar-collapse " id="navbarTogglerDemo03">
                <ul className="navbar-nav me-auto mb-2 mb-lg-0">
                    
                    <li className="nav-item">                    
                       
                        <NavLink  to='/' className="nav-link" >Home</NavLink>
                    </li>
                
                </ul>
                Not a member?
                <NavLink  to='/signup' className="btn btnn btn-primary" >Sign Up</NavLink>
            </div>
        </div>
        </nav>

    <div className='middle'>
        <div className='left'>
            <img src="images/signinpic.png" width="229px" alt="" />
            <div>
                <h2 className='head'>
                Sign in and stay informed of <br /> your childs vaccine schedule.
                
                </h2>
                <p className='para'>
                Invest in your childs health: <br /> get vaccinated and protect your family.
                </p>
            </div>
              
            
              
        </div>

        <div className='right'>
            
            <form className="row g-3 container">

            <h3>Sign in to Vaccamate</h3>
            
            

            <div className="col-md-7">
                <label htmlfor="Ahaar Number" className="form-label">Aadhar Number</label>
                <input type="Number" className="form-control" id="Aadhar" required/>
            </div>

            
            <div className="col-md-7">
                <label htmlfor="inputPassword4" className="form-label">Create Password</label>
                <input type="password" className="form-control" id="inputPassword4" required/>
            </div>

            

            <div className="col-12 d-grid gap-2 d-md-block">
                <NavLink  to='/profile' className="btn btn-primary" >Sign In</NavLink>
            </div>
            </form>

        </div>
    </div>
    </div>
  )
}

export default Signin