import React from 'react'
import './Navbar.css';
import './Signup.css';
import { BrowserRouter as Router, Routes, Route, NavLink } from "react-router-dom";


function Signin() {
  return (
    
    <div >
        
        <nav id="home" className="navbar navbar-expand-lg bg-body-tertiary">
        <div className="container-fluid">
            <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarTogglerDemo03" aria-controls="navbarTogglerDemo03" aria-expanded="false" aria-label="Toggle navigation">
            <span className="navbar-toggler-icon"></span>
            </button>
            <a className="navbar-brand">
                <img src="images/wordmark.png" width="120px" alt="VaccAmate" className="d-inline-block align-text-top" />
              </a>
              
            <div className="collapse navbar-collapse " id="navbarTogglerDemo03">
                <ul className="navbar-nav me-auto mb-2 mb-lg-0">
                    
                    <li className="nav-item">                    
                        <NavLink  to='/' className="nav-link" >Home</NavLink>
                    </li>
                
                </ul>
                Already Registered?   
                
                <NavLink  to='/signin' className="btn btnn btn-primary" >Sign In</NavLink>
            </div>
        </div>
        </nav>

    <div className='middle'>
        <div className='left'>
            <img src="images/signuppic.png" width="229px" alt="" />
            <div>
                <h2 className='head'>
                Take control of your childs <br /> health: sign up now!
                </h2>
                <p className='para'>
                Stay one step ahead of disease: sign up <br /> now to easily access all your vaccination <br /> information right here!
                </p>
            </div>
              
            
              
        </div>

        <div className='right'>
            
            <form className="row g-3 container">

            <h3>Sign up to Vaccamate</h3>
            
            <div class="col-12">
                <label htmlfor="Name" className="form-label">Name</label>
                <input type="text" class="form-control" id="pName" placeholder="Name" required/>
            </div>

            <div className="col-md-6">
                <label htmlfor="Ahaar Number" className="form-label">Aadhar Number</label>
                <input type="Number" className="form-control" id="Aadhar" required/>
            </div>

            <div className="col-md-6">
                <label htmlfor="Phone Number" className="form-label">Phone Number</label>
                <input type="tel" className="form-control" id="PhoneNumber" required/>
            </div>

            <div className="col-12">
                <label htmlfor="inputAddress2" className="form-label">Address</label>
                <input type="text" className="form-control" id="inputAddress2" required/>
            </div>

            <div className="col-md-6">
                <label htmlfor="inputPassword4" className="form-label">Create Password</label>
                <input type="password" className="form-control" id="inputPassword4" required/>
            </div>

            <div className="col-md-6">
                <label htmlfor="inputPassword4" className="form-label">Confirm Password</label>
                <input type="password" className="form-control" id="inputPassword4" required/>
            </div>

            <div className="col-12 d-grid gap-2 d-md-block">
                <NavLink  to='/profile' className="btn btn-primary" >Create Account</NavLink>
            </div>
            </form>

        </div>
    </div>
    
    </div>
  )
}

export default Signin